import React, { useState, useEffect, useContext } from 'react';
import { APIContext } from '../../backend/APIContext';
import { UserContext } from '../../backend/UserContext';
import { useParams } from 'react-router';
import GroupMembersTable from '../../components/Tables/GroupMembersTable';
import UserReceiptsTable from '../../components/Tables/UserReceiptsTable';
import { Auth } from 'aws-amplify';
import AddReceipt from '../../components/Dialogs/AddReceipt';
import MemberFinish from '../../components/Dialogs/MemberFinish';
// MUI
import { Grid } from '@mui/material';
import { Stack } from '@mui/material';

const AdminView = () => {
	const { fetchGroupMembers } = useContext(APIContext);
	const { groupnr } = useParams('');
	const [ groupMembers, setGroupMembers ] = useState([]);
	const [ membersFetched, setMembersFetched ] = useState(false);
	const [ role ] = useState('admin');
	const [ userFinished, setUserFinished ] = useState();
	const [ userReceipts, setUserReceipts ] = useState([]);
	const [ groupFinalized, setGroupFinalized ] = useState(false);

	const { userEmail, setUserEmail, setUserLoggedIn } = useContext(UserContext);
	const { fetchFinalizedGroupLogs, fetchUserGroupReceipts } = useContext(APIContext);

	const listGroupMembers = async () => {
		fetchGroupMembers(groupnr)
			.then((members) => {
				const membersArr = [];
				members.forEach((member) => {
					membersArr.push({ ...member });
				});
				return membersArr;
			})
			.then((membersArr) => {
				setGroupMembers(membersArr);
			});
	};

	useEffect(() => {
		Auth.currentAuthenticatedUser({
			bypassCache: true
		}).then((response) => {
			fetchUserGroupReceipts(groupnr, response.attributes.email).then((userData) => {
				setUserLoggedIn(true);
				setUserEmail(userData.Item.memberID);
				setUserFinished(userData.Item.isFinished);
				setUserReceipts([ ...userReceipts, ...userData.Item.memberReceipts ]);
			});
		});
		listGroupMembers();
		return () => {
			setMembersFetched(true);
			fetchFinalizedGroupLogs(groupnr, userEmail, 'admin').then((response) => {
				if (response.Item.isFinalized.BOOL === true) {
					setGroupFinalized(true);
				}
			});
		};
	}, []);

	return (
		<React.Fragment>
			<h2 className="text-justify">group {groupnr}</h2>
			<Stack direction="row" justifyContent="center" spacing={10}>
				<Grid item xs={6} md={8}>
					{membersFetched === true ? (
						<GroupMembersTable
							groupMembersArr={groupMembers}
							groupID={groupnr}
							groupFinalized={groupFinalized}
						/>
					) : (
						<h2>loading...</h2>
					)}
				</Grid>
				<Grid item xs={6} md={8}>
					<UserReceiptsTable groupnr={groupnr} userEmail={userEmail} role={role} />
					<div id="groupviewbuttons">
						{userFinished === true ? (
							<div className="text-justify">
								<h3>you finished adding your receipts</h3>
								<h5>
									if you have more receipts to add, <br /> contact group creator to allow you again.
								</h5>
							</div>
						) : (
							<Stack spacing={2} direction="row">
								<AddReceipt groupID={groupnr} role={role} />{' '}
								<MemberFinish
									variant="contained"
									groupID={groupnr}
									memberID={userEmail}
									role={role}
									memberReceipts={userReceipts}
								/>
							</Stack>
						)}
					</div>
				</Grid>
			</Stack>
		</React.Fragment>
	);
};

export default AdminView;
