import React from 'react';
import { UserContext } from '../../backend/UserContext';
import { APIContext } from '../../backend/APIContext';
import { useNavigate } from 'react-router-dom';
// MUI
import Box from '@mui/material/Box';
import { FormControl } from '@mui/material';
import { InputLabel } from '@mui/material';
import { Input } from '@mui/material';
import { Button } from '@mui/material';

const GroupJoin = () => {
  const { groupJoiner } = React.useContext(APIContext);
  const { userName, userEmail } = React.useContext(UserContext);
  const [joinGroupNr,setJoinGroupNr] = React.useState("");
  const navigate = useNavigate();


  const joinGroup = (event) => {
    event.preventDefault();
    groupJoiner(joinGroupNr, userEmail, userName, "true");
    navigate(`/group/view/member/${joinGroupNr}`);

  }


	return (
		<React.Fragment>
			<h1 id="joinlanding">pay it simple silly. </h1>
			<h3>you can also invite other friends to the group</h3>
			<div id="joinboxes">
				<div id="joinbox">
					<Box
						component="form"
						onSubmit={joinGroup}
						width={400}
						height={150}
						sx={{ boxShadow: 6, p: 3, mt:2 }}
					>
						<FormControl sx={{ mt: 1, width: 300 }}>
							<InputLabel htmlFor="joinGroupNr">Group Number</InputLabel>
							<Input
								required
								id="joinGroupNr"
								value={joinGroupNr}
								onChange={(e) => setJoinGroupNr(e.target.value)}
								variant="filled"
							/>
						</FormControl>
						<br />
						<Button sx={{ mt: 2 }} type="submit" variant="contained">
							Let's Join!
						</Button>
					</Box>
				</div>
			</div>
		</React.Fragment>
	);
};

export default GroupJoin;
