import React, { createContext } from 'react';
import fetch from 'node-fetch';
import uniqueRandom from 'unique-random';

const APIContext = createContext();

const APIProvider = (props) => {
	const APIroot = 'https://h2genw4y7b.execute-api.eu-central-1.amazonaws.com/dev';

	const groupCreator = async (userMail, userName) => {
		const random = uniqueRandom(100000, 999999);
		const newGroupID = random().toString();
		const startCall = await fetch(`${APIroot}/group/start`, {
			method: 'post',
			body: JSON.stringify({
				groupID: newGroupID,
				groupAdmin: userMail,
				memberName: userName,
				groupMembers: [ userMail ]
			}),
			headers: { 'Content-Type': 'application/json' }
		});
		const callResponse = await startCall.json();
		const data = JSON.parse(callResponse.body);
		const groupnr = data.groupID;
		return groupnr;
	};

	const groupJoiner = async (groupID, memberID, memberName, isNonAdmin) => {
		const joinCall = await fetch(`${APIroot}/group/join`, {
			method: 'post',
			body: JSON.stringify({
				groupID,
				memberID,
				memberName,
				isNonAdmin
			}),
			headers: { 'Content-Type': 'application/json' }
		});
		const callResponse = await joinCall.json();
		return callResponse;
	};

	const groupFinalizer = async (membersArr, groupID) => {
		const finalizeCall = await fetch(`${APIroot}/group/finalize`, {
			method: 'post',
			body: JSON.stringify({
				membersArr,groupID
			}),
			headers: { 'Content-Type': 'application/json' }
		});
		const callResponse = await finalizeCall.json();
		return callResponse;
	};

	const fetchUserGroups = async (userMail) => {
		const response = await fetch(`${APIroot}/user?userID=${userMail}`, {
			method: 'GET',
			headers: {
				accept: 'application/json'
			}
		});
		if (!response.ok) {
			throw new Error(`Error! status: ${response.status}`);
		}
		const result = await response.json();
		const groupsAdmined = result.Item.userGroupsAdmined;
		const groupsMembered = result.Item.userGroupsMembered;
		return { groupsAdmined, groupsMembered };
	};

	const fetchUserGroupReceipts = async (groupnr, email) => {
		const response = await fetch(`${APIroot}/receipt?groupID=${groupnr}&userID=${email}`, {
			method: 'GET',
			headers: {
				accept: 'application/json'
			}
		});

		if (!response.ok) {
			throw new Error(`Error! status: ${response.status}`);
		}
		const result = await response.json();
		return result;
	};

	const fetchGroupMembers = async (groupID) => {
		try {
			const response = await fetch(`${APIroot}/group?groupID=${groupID}`, {
				method: 'GET',
				headers: {
					accept: 'application/json'
				}
			});

			if (!response.ok) {
				throw new Error(`Error! status: ${response.status}`);
			}
			const result = await response.json();
			return result;
		} catch (err) {
			console.log(err);
		}
	};

	const fetchFinalizedGroupLogs = async(groupID, userID, role) => {
		const response = await fetch(`${APIroot}/group/finalize?groupID=${groupID}`, {
			method: 'GET',
			headers: {
				accept: 'application/json'
			}
		});

		if (!response.ok) {
			throw new Error(`Error! status: ${response.status}`);
		}
		const result = await response.json();
		if (role === "member") {
			//
		} else if (role === "admin") {
		return result;
		}
	};

	const receiptAdder = async (groupID, memberID, note, date, amount) => {
		const addCall = await fetch(`${APIroot}/receipt/add`, {
			method: 'post',
			body: JSON.stringify({
				groupID,
				memberID,
				note,
				date,
				amount
			}),
			headers: { 'Content-Type': 'application/json' }
		});
		const callResponse = await addCall.json();
		return callResponse;
	};

	const receiptDeleter = async (groupID, memberID, listNum) => {
		const deleteCall = await fetch(`${APIroot}/receipt/delete`, {
			method: 'post',
			body: JSON.stringify({
				groupID,
				memberID,
				listNum
			}),
			headers: { 'Content-Type': 'application/json' }
		});
		const callResponse = await deleteCall.json();
		return callResponse;
	};

	const memberFinishToggle = async (groupID, memberID, toFinish) => {
		const finishCall = await fetch(`${APIroot}/user/member`, {
			method: 'post',
			body: JSON.stringify({
				groupID,
				memberID,
				toFinish
			}),
			headers: { 'Content-Type': 'application/json' }
		});
		const callResponse = await finishCall.json();
		return callResponse;
	};

	const receiptFinishToggle = async (groupID, memberID, memberReceipts, action) => {
		var total = 0;
		if (action === 'finish') {
			memberReceipts.map((receipt) => {
				total += receipt[2];
				return total;
			});
		}
		const finishCall = await fetch(`${APIroot}/receipt/finish`, {
			method: 'post',
			body: JSON.stringify({
				groupID,
				memberID,
				totalNum: total.toString(),
				action
			}),
			headers: { 'Content-Type': 'application/json' }
		});
		const callResponse = await finishCall.json();
		return callResponse;
	};

	const userParams = {
		groupCreator,
		groupJoiner,
		groupFinalizer,
		fetchUserGroups,
		fetchUserGroupReceipts,
		fetchGroupMembers,
		fetchFinalizedGroupLogs,
		receiptAdder,
		receiptDeleter,
		memberFinishToggle,
		receiptFinishToggle
	};

	return <APIContext.Provider value={userParams}>{props.children}</APIContext.Provider>;
};

export { APIContext, APIProvider };
