import React, { useState, useContext, useEffect } from 'react';
import { APIContext } from '../../backend/APIContext';
import GroupFinalize from '../Dialogs/GroupFinalize';
// MUI
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import ReplayIcon from '@mui/icons-material/Replay';
import Tooltip from '@mui/material/Tooltip';

const GroupMembersTable = ({ groupID, groupMembersArr, groupFinalized }) => {
	const { memberFinishToggle, receiptFinishToggle } = useContext(APIContext);
	const [ allMembersFinished, setAllMembersFinished ] = useState(true);

	const finishToggle = (memberReceipts, memberID, toggle) => {
		var action = 'finish';
		toggle === 'false' && (action = 'revoke');
		memberFinishToggle(groupID, memberID, toggle);
		receiptFinishToggle(groupID, memberID, memberReceipts, action);
		setTimeout(() => {
			window.location.reload();
		}, 550);
	};

	const checkFinishStatus = (memberReceipts) => {
		memberReceipts.map((member) => {
			if (member.isFinished === false) {
				setAllMembersFinished(false);
			}
			return member;
		});
	};

	useEffect(
		() => {
			checkFinishStatus(groupMembersArr);
		},
		[ groupMembersArr ]
	);

	return (
		<React.Fragment>
			<TableContainer component={Paper}>
				<Table sx={{ maxWidth: 450 }} aria-label="simple table">
					<TableHead>
						<TableRow>
							<TableCell align="center">E-mail</TableCell>
							<TableCell align="center">Name</TableCell>
							<TableCell align="center">Status</TableCell>
							<TableCell align="center">Total Expense</TableCell>
						</TableRow>
					</TableHead>
					<TableBody>
						{groupMembersArr.map((member) => (
							<TableRow key={member.memberID} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
								<TableCell align="center">{member.memberID}</TableCell>
								<TableCell align="center">{member.memberName}</TableCell>
								<TableCell align="center">
									{member.isFinished ? (
										<div>
											Finished{' '}
											<Tooltip title="Return User" placement="bottom">
												<ReplayIcon
													sx={{ cursor: 'pointer' }}
													onClick={() => finishToggle(0, member.memberID, 'false')}
												/>
											</Tooltip>
										</div>
									) : (
										<div>
											In Progress{' '}
											<Tooltip title="Finish User" placement="bottom">
												<CheckCircleOutlineIcon
													sx={{ cursor: 'pointer' }}
													onClick={() =>
														finishToggle(member.memberReceipts, member.memberID, 'true')}
												/>
											</Tooltip>
										</div>
									)}
								</TableCell>
								{member.isFinished && <TableCell align="center">{member.totalExpense}</TableCell>}
							</TableRow>
						))}
					</TableBody>
				</Table>
			</TableContainer>
			<br />
			{allMembersFinished ? groupFinalized === true ? (
				<div>
					<h4>this group is finalized!</h4>
					<h5>
						<a style={{ 'text-decoration': 'none', color: 'black' }} href={`/group/final/${groupID}`}>
							click here
						</a>{' '}
						to see the result in details
					</h5>
				</div>
			) : (
				<div className="text-justify">
					<h4 component="div">all users submitted their receipts! </h4>
					<GroupFinalize sx={{ width: 200, mt: 2 }} membersArr={groupMembersArr} groupID={groupID} />
				</div>
			) : (
				<div>
					<h4>some users are still not finished</h4>
					<h5>
						you can contact group members to submit their receipts or you can finish their progress above.
					</h5>
				</div>
			)}
		</React.Fragment>
	);
};

export default GroupMembersTable;
