import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.css';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
// Context Providers
import { UserProvider } from './backend/UserContext';
import { APIProvider } from './backend/APIContext';
// Amplify
import aws_exports from './aws-exports';
import Amplify from 'aws-amplify';
// mui
import { ThemeProvider, createTheme } from '@mui/material/styles';
// pages import
import NavMenu from './components/NavMenu/NavMenu';
import Home from './pages/Home';
import Register from './pages/Register';
import Login from './pages/Login';
import GroupCreating from './pages/group/GroupCreating';
import GroupJoin from './pages/group/GroupJoin';
import GroupContinue from './pages/group/GroupContinue';
import AdminView from './pages/group/AdminView';
import MemberView from './pages/group/MemberView';
import MemberFinishPort from './components/Dialogs/MemberFinishPort';
import AddReceiptPort from './components/Dialogs/AddReceiptPort';
import DeleteReceiptPort from './components/Dialogs/DeleteReceiptPort';
import GroupFinalizedAdminTable from './components/Tables/GroupFinalizedAdminTable';

Amplify.configure(aws_exports);

function App() {
	const muiTheme = createTheme({
		typography: {
			allVariants: {
				fontFamily: 'Montserrat',
				fontWeight: '500',
				textTransform: 'none'
			}
		}
	});

	return (
		<React.Fragment>
			<UserProvider>
				<APIProvider>
					<ThemeProvider theme={muiTheme}>
						<Router>
							<section className="layout">
								<div className="header">
									<NavMenu />
								</div>
								<div className="leftSide" />
								<div className="body">
									<Routes>
										<Route path="/" exact element={<Home />} />
										<Route path="/register" element={<Register />} />
										<Route path="/login" element={<Login />} />
										<Route path="/group/create/:groupnr" element={<GroupCreating />} />
										<Route path="/group/join" element={<GroupJoin />} />
										<Route path="/viewgroups" element={<GroupContinue />} />
										<Route path="/group/view/admin/:groupnr" element={<AdminView />} />
										<Route path="/group/view/member/:groupnr" element={<MemberView />} />
										<Route path="/receipt/add/:role/:groupnr" element={<AddReceiptPort />} />
										<Route path="/receipt/delete/:role/:groupnr" element={<DeleteReceiptPort />} />
										<Route path="/user/finish/:role/:groupnr" element={<MemberFinishPort />} />
										<Route path="group/final/:groupnr" element={<GroupFinalizedAdminTable />} />
									</Routes>
								</div>
								<div className="rightSide" />
							</section>
						</Router>
					</ThemeProvider>
				</APIProvider>
			</UserProvider>
		</React.Fragment>
	);
}

export default App;
