import React, { useState, useContext, useEffect } from 'react';
import { useParams } from 'react-router';
import { UserContext } from '../../backend/UserContext';
import { APIContext } from '../../backend/APIContext';
import { Auth } from 'aws-amplify';
//MUI
import Stack from '@mui/material/Stack';
// COMPONENTS
import AddReceipt from '../../components/Dialogs/AddReceipt';
import MemberFinish from '../../components/Dialogs/MemberFinish';
import UserReceiptsTable from '../../components/Tables/UserReceiptsTable';

const MemberView = ({ admGroupID }) => {
	var { groupnr } = useParams('');
	if (admGroupID !== undefined) {
		groupnr = admGroupID;
	}

	const [ userFinished, setUserFinished ] = useState();
	const [ role ] = useState('member');

	const { userEmail, setUserEmail, setUserLoggedIn } = useContext(UserContext);
	const { fetchUserGroupReceipts } = useContext(APIContext);
	const [ userReceipts, setUserReceipts ] = useState([]);

	useEffect(() => {
		Auth.currentAuthenticatedUser({
			bypassCache: true
		}).then((response) => {
			fetchUserGroupReceipts(groupnr, response.attributes.email)
				.then(async (userData) => {
					setUserLoggedIn(true);
					await setUserEmail(userData.Item.memberID);
					setUserFinished(userData.Item.isFinished);
					return userData.Item.memberReceipts;
				})
				.then((receipts) => {
					const newReceipts = receipts.map((receipt) => receipt);
					setUserReceipts([ ...userReceipts, ...newReceipts ]);
					console.log(userReceipts);
				});
		});
		return () => {
			console.log(userReceipts);
		};
	}, []);

	return (
		<React.Fragment>
			<div className="text-justify">
				<h2>group {groupnr}</h2>
				<br />
			</div>
			<UserReceiptsTable groupnr={groupnr} userEmail={userEmail} role={role} />
			<div id="groupviewbuttons">
				{userFinished === true ? (
					<div className="text-justify">
						<h3>you finished adding your receipts</h3>
						<h5>
							if you have more receipts to add, <br /> contact group creator to allow you again.
						</h5>
					</div>
				) : (
					<Stack spacing={2} direction="row">
						<AddReceipt groupID={groupnr} role={role} />{' '}
						<MemberFinish
							variant="contained"
							groupID={groupnr}
							memberID={userEmail}
							role={role}
							memberReceipts={userReceipts}
						/>
					</Stack>
				)}
			</div>
		</React.Fragment>
	);
};

export default MemberView;
