import React, { useEffect, useContext } from 'react';
import { UserContext } from '../backend/UserContext';
import GuestHome from './GuestHome';
import UserHome from './UserHome';
import { Auth } from 'aws-amplify';

function Home() {
    const { setUserEmail, setUserName, userLoggedIn, setUserLoggedIn } = useContext(UserContext);


	useEffect(() => {
		Auth.currentAuthenticatedUser({
			bypassCache: true
		})
			.then((response) => {
                setUserEmail(response.attributes.email);
                setUserName(response.attributes.name);
                setUserLoggedIn(true);
            })
			.catch((err) => console.log(err));
	}, [setUserEmail,setUserName,setUserLoggedIn]);

	if (userLoggedIn) {
		return (
			<div>
				<UserHome />
			</div>
		);
	} else {
		return (
			<div>
				<GuestHome />
			</div>
		);
	}
}

export default Home;
