import React from 'react';
import { APIContext } from '../../backend/APIContext';
import { UserContext } from '../../backend/UserContext';
import { useParams } from 'react-router';
import { useNavigate } from 'react-router';
import QRCode from 'react-qr-code';

const GroupCreating = () => {
	const { groupJoiner } = React.useContext(APIContext);
	const { userName, userEmail } = React.useContext(UserContext);
	const { groupnr } = useParams('');
	const [ userAdded, setUserAdded ] = React.useState(false);
    const navigate = useNavigate();

	setTimeout(() => {
		groupJoiner(groupnr, userEmail, userName, 'false');
		setUserAdded(true);
	}, 3000);

    const visitGroupCreated = () => {
        navigate(`/group/view/admin/${groupnr}`)
    }

	return (
		<React.Fragment>
			{userAdded ? (
				<div>
					<h3>group {groupnr} is created! </h3>
					<h1>
						<QRCode  value={`https://simplespl.it/group/join/${groupnr}`} />
					</h1>
					<br />
					<h4>share the QR code or simplespl.it/{groupnr} to invite your friends</h4>
					<br />{' '}
					<h4 style={{ cursor: 'pointer' }} onClick={() => visitGroupCreated()}>
						visit the group
					</h4>
				</div>
			) : (
				<div>
					<h3>creating Group {groupnr} </h3>
					<br /> <h4>little more patience please...</h4>
				</div>
			)}
		</React.Fragment>
	);
};

export default GroupCreating;
