import React, { useState, useContext, useEffect } from 'react';
import { APIContext } from '../../backend/APIContext';
import { UserContext } from '../../backend/UserContext';
import UserGroupsTable from '../../components/Tables/UserGroupsTable';
// MUI
import Stack from '@mui/material/Stack';

const GroupContinue = () => {
	const { fetchUserGroups } = useContext(APIContext);
	const { userEmail } = useContext(UserContext);
	const [ groupsAdmined, setGroupsAdmined ] = useState([]);
	const [ groupsMembered, setGroupsMembered ] = useState([]);
	const [ groupsFetched, setGroupsFetched ] = useState(false);
	const admin = "admin"
	const member = "member"

	const acqUserGroups = async () => {
		await fetchUserGroups(userEmail).then((ug) => {
			setGroupsAdmined(ug.groupsAdmined);
			setGroupsMembered(ug.groupsMembered);
			setGroupsFetched(true);
		});
	};

	useEffect(() => {
		acqUserGroups();
	}, []);

	if (groupsFetched && groupsAdmined !== undefined && groupsMembered !== undefined) {
		return (
			<React.Fragment>
				<Stack direction="row" justifyContent="center" spacing={3}>
					<UserGroupsTable groupArr={groupsAdmined} role={admin} />
					<UserGroupsTable groupArr={groupsMembered} role={member} />
				</Stack>
			</React.Fragment>
		);
	} else if (groupsFetched && groupsAdmined !== undefined) {
		return <UserGroupsTable groupArr={groupsAdmined} role={admin} />;
	} else if (groupsFetched && groupsMembered !== undefined) {
		return <UserGroupsTable groupArr={groupsMembered} role={member} />;
	} else if (groupsFetched && groupsAdmined === undefined && groupsMembered === undefined) {
		return <h3>You don't have any groups yet... <span role="img" aria-label="oops"></span>  </h3>;
	} else {
		return <div>Loading...</div>;
	}
};

export default GroupContinue;
