import React, { useState, useContext } from 'react';
import { APIContext } from '../../backend/APIContext';
import { useNavigate } from 'react-router-dom';
//MUI
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { DialogContentText } from '@mui/material';
import RocketLaunchIcon from '@mui/icons-material/RocketLaunch';

function GroupFinalize({ membersArr, groupID }) {
	const [ open, setOpen ] = useState(false);
	const { groupFinalizer } = useContext(APIContext);
	const navigate = useNavigate();

	const handleClickOpen = () => {
		setOpen(true);
	};

	const handleClose = () => {
		setOpen(false);
	};

	const finalizeTheGroup = (memArr,grID) => {
		console.log(memArr);
		groupFinalizer(memArr,grID).then(finalResponse => {
			navigate(`/group/final/${groupID}`)
		})
	};

	return (
		<div>
			<Button
				onClick={handleClickOpen}
				sx={{ width: 200, mt: 2 }}
				variant="contained"
				endIcon={<RocketLaunchIcon />}
			>
				Split The Costs
			</Button>
			<Dialog open={open} onClose={handleClose}>
				<DialogTitle>finalize the group</DialogTitle>
				<DialogContent>
					<DialogContentText id="alert-dialog-description">
						Do you confirm all users added their receipts?
						<br />
						Once confirmed, each member will receive their transaction info
					</DialogContentText>
				</DialogContent>
				<DialogActions>
					<Button
						variant="contained"
						sx={{ bgcolor: 'purple' }}
						onClick={() => finalizeTheGroup(membersArr,groupID)}
						autoFocus
					>
						let's go!
					</Button>
				</DialogActions>
			</Dialog>
		</div>
	);
}

export default GroupFinalize;
