import React, { useState } from 'react';
import './HomeRegisterLogin.css';
import Box from '@mui/material/Box';
import { FormControl } from '@mui/material';
import { InputLabel } from '@mui/material';
import { Input } from '@mui/material';
import { FormControlLabel } from '@mui/material';
import { Checkbox } from '@mui/material';
import { Button } from '@mui/material';
import { Auth } from 'aws-amplify';
import { useNavigate } from 'react-router-dom';

function Register() {
	const navigate = useNavigate();
	const [ name, setName ] = useState('');
	const [ email, setEmail ] = useState('');
	const [ password, setPassword ] = useState('');
	const [ confirmationCode, setConfirmationCode ] = useState('');
	const [ regFormSent, setRegFormSent ] = useState(false);

	const registrationFormSubmitted = async (event) => {
		event.preventDefault();
		setRegFormSent(true);
		await Auth.signUp({
			username: email,
			password,
			attributes: {
				name: name
			}
		});
	};

	const confirmationCodeSent = async (event) => {
		event.preventDefault();
		await Auth.confirmSignUp(email, confirmationCode);
		navigate('/login');
	};

	return (
		<React.Fragment>
			<h1 id="landingmain">one final step before the simplicity </h1>
			<h3>
				simplesplit is a complete cloud native app
				<br /> delivered to you with joy and excitement!
			</h3>
			<div id="registerboxes">
				<div id="registerbox">
					<Box
						component="form"
						onSubmit={registrationFormSubmitted}
						width={400}
						height={350}
						sx={{ boxShadow: 6, p: 3 }}
					>
						<h4 id="registerboxtitle">registration</h4>
						<FormControl sx={{ mt: 1, width: 300 }}>
							<InputLabel htmlFor="name">Your Name</InputLabel>
							<Input
								required
								id="name"
								value={name}
								onChange={(e) => setName(e.target.value)}
								variant="standard"
							/>
						</FormControl>
						<FormControl sx={{ mt: 1, width: 300 }}>
							<InputLabel htmlFor="email">Your E-mail</InputLabel>
							<Input
								required
								id="email"
								value={email}
								onChange={(e) => setEmail(e.target.value)}
								variant="filled"
							/>
						</FormControl>
						<FormControl sx={{ mt: 1, width: 300 }}>
							<InputLabel htmlFor="Set Password">Set Password</InputLabel>
							<Input
								id="password"
								value={password}
								onChange={(e) => setPassword(e.target.value)}
								type="password"
								variant="standard"
							/>
						</FormControl>
						<FormControl sx={{ mt: 1 }}>
							<FormControlLabel
								control={<Checkbox defaultChecked />}
								label="I accept terms & conditions"
							/>
						</FormControl>
						<Button sx={{ mt: 2 }} type="submit" variant="contained">
							Register
						</Button>
					</Box>
				</div>
				{regFormSent && (
					<div id="registerconfirmbox">
						<Box
							component="form"
							onSubmit={confirmationCodeSent}
							width={400}
							height={230}
							sx={{ boxShadow: 6, p: 3 }}
						>
							<h5>
								You will receive a confirmation code <br />
								to your e-mail in a few seconds. <br />
								Please enter the code below.
							</h5>
							<FormControl sx={{ mt: 1 }}>
								<InputLabel htmlFor="name">Confirmation Code</InputLabel>
								<Input
									required
									id="confirmation-code"
									value={confirmationCode}
									onChange={(e) => setConfirmationCode(e.target.value)}
									variant="standard"
								/>
							</FormControl>
							<br />
							<Button sx={{ mt: 3 }} type="submit" variant="contained">
								Confirm
							</Button>
						</Box>
					</div>
				)}
			</div>
		</React.Fragment>
	);
}

export default Register;
