import React, { useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { UserContext } from '../../backend/UserContext';
import { APIContext } from '../../backend/APIContext';
import { Auth } from 'aws-amplify';

const UserNavMenu = () => {
	const { userName, userEmail } = useContext(UserContext);
	const { groupCreator } = useContext(APIContext);
	const navigate = useNavigate();

	const createGroup = () => {
		groupCreator(userEmail, userName).then((groupnr) => {
			navigate(`/group/create/${groupnr}`);
		});
	};

	const userLogOut = async () => {
		await Auth.signOut();
		navigate(`/`);
		window.location.reload();
	};

	return (
		<React.Fragment>
			<div className="header px-3">
				<nav className="navbar navbar-expand-lg navbar-light bg-light h-100 ml-1">
					<a className="navbar-brand" href="/">
						<img
							src={require('./ssbnw-1.png')}
							width="40"
							height="40"
							className="d-inline-block align-middle"
							alt=""
						/>
						<span className="logo-text">Simple Split</span>
					</a>
					<button
						className="navbar-toggler"
						type="button"
						data-toggle="collapse"
						data-target="#navbarSupportedContent"
						aria-controls="navbarSupportedContent"
						aria-expanded="false"
						aria-label="Toggle navigation"
					>
						<span className="navbar-toggler-icon" />
					</button>
					<div className="collapse navbar-collapse d-flex justify-content-evenly" id="navbarSupportedContent">
						<ul className="navbar-nav mr-auto d-flex">
							<li className="nav-item align-bottom active">
								<button className="nav-link text-wrap btn btn-link" onClick={() => navigate('/')}>
									Home
								</button>
							</li>
							<li className="nav-item">
								<button className="nav-link text-wrap btn btn-link" onClick={() => createGroup()}>
									Create Group
								</button>
							</li>
							<li className="nav-item">
								<button
									className="nav-link text-wrap btn btn-link"
									onClick={() => navigate('/group/join')}
								>
									Join Group
								</button>
							</li>
							<li className="nav-item">
								<button
									className="nav-link text-wrap btn btn-link"
									onClick={() => navigate('/viewgroups')}
								>
									View Groups
								</button>
							</li>
						</ul>
					</div>
					<form className="form-inline">
						<button
							type="button"
							className="btn btn-outline-primary mx-3"
							onClick={() => console.log('createdGroupID')}
						>
							Account
						</button>
						<button type="button" className="btn btn-secondary mr-3" onClick={() => userLogOut()}>
							Logout
						</button>
					</form>
				</nav>
			</div>
		</React.Fragment>
	);
};

export default UserNavMenu;
