import React, { useState, useEffect, useContext } from 'react';
import { useParams } from 'react-router';
import { Auth } from 'aws-amplify';
import { APIContext } from '../../backend/APIContext';
import { UserContext } from '../../backend/UserContext';
// MUI
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';


const GroupFinalizedAdminTable = () => {
	const { fetchFinalizedGroupLogs } = useContext(APIContext);
	const { userEmail, setUserEmail, setUserLoggedIn } = useContext(UserContext);
	const [ transferLog, setTransferLog ] = useState([]);
	const [ groupAdmin, setGroupAdmin ] = useState('');
	const [ isFinalized, setIsFinalized ] = useState(false);

	var { groupnr } = useParams('');

	useEffect(() => {
		Auth.currentAuthenticatedUser({
			bypassCache: true
		}).then((response) => {
			setUserEmail(response.attributes.email);
			setUserLoggedIn(true);
		});
		return () => {
			fetchFinalizedGroupLogs(groupnr, userEmail, 'admin').then((response) => {
				const transferLogStr = JSON.parse(response.Item.finalTransferLog.S);
				const groupAdmin = response.Item.groupAdmin.S;
				setTransferLog(transferLogStr);
				setGroupAdmin(groupAdmin);
				setIsFinalized(response.Item.isFinalized.BOOL);
			});
		};
	}, []);

	if (groupAdmin !== userEmail) {
		return (
			<React.Fragment>
				<h3>you are not authorized for {groupnr}</h3>
				<h4>please contact group creator for more info</h4>
			</React.Fragment>
		);
	} else if (isFinalized === false) {
		return (
			<React.Fragment>
				<h3>group {groupnr} is still not finalized</h3>
				<h4>please contact group creator to learn more</h4>
			</React.Fragment>
		);
	} else if (isFinalized === true) {
		return (
			<React.Fragment>
				<h3>
					group {groupnr} is finalized!
					<span role="img" aria-label="yeah!">
						
					</span>{' '}
				</h3>
				<h4>
					each member will receive their <br />transfer details as follows:
				</h4>
                <div id="finalizedadmintable">
                <TableContainer sx={{ maxWidth: 450 }} component={Paper}>
				<Table aria-label="simple table">
					<TableHead align="center">
						<TableRow align="center">
							<TableCell align="center" colSpan={2}>
								transfers to take place
							</TableCell>
						</TableRow>
					</TableHead>
					<TableBody>
						{transferLog.map((transfer,index) => (
							<TableRow key={index} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
								<TableCell align="center">{
                                    <h5>{transfer.sender} should send {transfer.amount} to {transfer.receiver}</h5>
                                }</TableCell>
							</TableRow>
						))}
					</TableBody>
				</Table>
			</TableContainer>
                </div>
			</React.Fragment>
		);
	}
};

export default GroupFinalizedAdminTable;
