import React, { useState, useContext } from 'react';
import './HomeRegisterLogin.css';
// MUI
import Box from '@mui/material/Box';
import { FormControl } from '@mui/material';
import { InputLabel } from '@mui/material';
import { Input } from '@mui/material';
import { Button } from '@mui/material';
// packages
import { Auth } from 'aws-amplify';
import { useNavigate } from 'react-router-dom';

// Contexts
import { UserContext } from '../backend/UserContext';

function Login() {
	const [ email, setEmail ] = useState('');
	const [ password, setPassword ] = useState('');

	const userContext = useContext(UserContext);
	const { setUserEmail, setUserName, setUserLoggedIn } = userContext;

	const navigate = useNavigate();

	const loginFormSubmitted = async (event) => {
		event.preventDefault();
		Auth.signIn({
			username: email,
			password
		})
			.then((response) => {
				if (response) {
					setUserEmail(response.attributes.email);
					setUserName(response.attributes.name);
					setUserLoggedIn(true);
				}
			})
			.finally(() => {
				navigate('/');
			});
	};

	return (
		<React.Fragment>
			<h1 id="landingmain">ready. set. pocket. </h1>
			<h3>thank you for experimenting simple split</h3>
			<div id="registerboxes">
				<div id="registerbox">
					<Box
						component="form"
						onSubmit={loginFormSubmitted}
						width={400}
						height={250}
						sx={{ boxShadow: 6, p: 3 }}
					>
						<h4 id="registerboxtitle">login</h4>
						<FormControl sx={{ mt: 1, width: 300 }}>
							<InputLabel htmlFor="email">E-mail</InputLabel>
							<Input
								required
								id="email"
								value={email}
								onChange={(e) => setEmail(e.target.value)}
								variant="filled"
							/>
						</FormControl>
						<FormControl sx={{ mt: 1, width: 300 }}>
							<InputLabel htmlFor="Set Password">Password</InputLabel>
							<Input
								id="password"
								value={password}
								onChange={(e) => setPassword(e.target.value)}
								type="password"
								variant="standard"
							/>
						</FormControl>
						<br />
						<Button sx={{ mt: 2 }} type="submit" variant="contained">
							Let's Go!
						</Button>
					</Box>
				</div>
			</div>
		</React.Fragment>
	);
}

export default Login;
