import React, { useContext, useState } from 'react';
import { UserContext } from '../../backend/UserContext';
import { APIContext } from '../../backend/APIContext';
import { useNavigate } from 'react-router-dom';
//MUI
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import InputAdornment from '@mui/material/InputAdornment';
import DialogTitle from '@mui/material/DialogTitle';
import { InputLabel } from '@mui/material';
import { OutlinedInput } from '@mui/material';

function AddReceipt({ groupID, role }) {
	const { userEmail } = useContext(UserContext);
	const { receiptAdder } = useContext(APIContext);
	const [ open, setOpen ] = useState(false);
	const [ note, setNote ] = useState('');
	const [ date, setDate ] = useState();
	const [ amount, setAmount ] = useState('');
	const navigate = useNavigate();

	const handleClickOpen = () => {
		setOpen(true);
	};

	const handleClose = () => {
		setOpen(false);
	};

	const addReceipt = () => {
		console.log(groupID, userEmail, note, date, amount);
		receiptAdder(groupID, userEmail, note, date, amount);
		setTimeout(() => {
			navigate(`/receipt/add/${role}/${groupID}`);
		}, 500);
	};

	return (
		<div>
			<Button variant="outlined" onClick={handleClickOpen}>
				Add Receipt
			</Button>
			<Dialog open={open} onClose={handleClose}>
				<DialogTitle>add receipt</DialogTitle>
				<DialogContent>
					<InputLabel htmlFor="name">note</InputLabel>
					<OutlinedInput
						autoFocus
						margin="dense"
						id="name"
						label="Note"
						fullWidth
						variant="standard"
						onChange={(e) => setNote(e.target.value)}
					/>

					<InputLabel htmlFor="date">date</InputLabel>
					<OutlinedInput
						margin="dense"
						id="date"
						type="date"
						fullWidth
						onChange={(e) => setDate(e.target.value)}
					/>

					<InputLabel htmlFor="amount">amount</InputLabel>
					<OutlinedInput
						id="amount"
						startAdornment={<InputAdornment position="start"></InputAdornment>}
						onChange={(e) => setAmount(e.target.value)}
					/>
				</DialogContent>
				<DialogActions>
					<Button onClick={addReceipt}>add receipt</Button>
				</DialogActions>
			</Dialog>
		</div>
	);
}

export default AddReceipt;
