import React from 'react';
import { useNavigate } from 'react-router-dom';
// MUI
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import ArrowCircleRightOutlinedIcon from '@mui/icons-material/ArrowCircleRightOutlined';

const UserGroupsTable = ({ groupArr, role }) => {
	const navigate = useNavigate();
	const [ roleText, setRoleText ] = React.useState('leading');

	React.useEffect(() => {
		role === 'member' && setRoleText('member of');
	}, []);

	const visitGroup = (groupnr, role) => {
		navigate(`/group/view/${role}/${groupnr}`);
	};

	return (
		<React.Fragment>
			<TableContainer sx={{ maxWidth: 250 }} component={Paper}>
				<Table aria-label="simple table">
					<TableHead align="center">
						<TableRow align="center">
							<TableCell align="center" colSpan={2}>
								groups you are {roleText}
							</TableCell>
						</TableRow>
					</TableHead>
					<TableBody>
						{groupArr.map((row) => (
							<TableRow key={row} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
								<TableCell align="center">{row}</TableCell>
								<TableCell
									sx={{ cursor: 'pointer' }}
									align="center"
									onClick={() => {
										visitGroup(row, role);
									}}
								>
									{<ArrowCircleRightOutlinedIcon />}
								</TableCell>
							</TableRow>
						))}
					</TableBody>
				</Table>
			</TableContainer>
		</React.Fragment>
	);
};

export default UserGroupsTable;
