import React, { useState, useEffect, useContext } from 'react';
import { APIContext } from '../../backend/APIContext';
import { useNavigate } from 'react-router-dom';
import { Auth } from 'aws-amplify';
//MUI
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import ClearOutlinedIcon from '@mui/icons-material/ClearOutlined';

const UserReceiptsTable = ({ groupnr, role }) => {
	const { fetchUserGroupReceipts, receiptDeleter } = useContext(APIContext);
	const [ userReceipts, setUserReceipts ] = useState([]);
	const [ userFinished, setUserFinished ] = useState();
	const navigate = useNavigate();

    const [ userEmail, setUserEmail ] = useState("");


    useEffect(() => {
		Auth.currentAuthenticatedUser({
			bypassCache: true
		})
			.then((response) => {
				setUserEmail(response.attributes.email);
                acqReceipts(response.attributes.email);
			})
			.catch((err) => console.log(err));
	}, []);

	const acqReceipts = async (userEmail) => {
		fetchUserGroupReceipts(groupnr, userEmail).then((userData) => {
			if (userData.Item !== undefined) {
				const memberReceipts = userData.Item.memberReceipts;
				setUserFinished(userData.Item.isFinished);
				var receiptRows = [];
				memberReceipts.map((receipt) => receiptRows.push({ ...receipt }));
				setUserReceipts(receiptRows);
			}
		});
	};

	const deleteReceipt = (recIndex) => {
		receiptDeleter(groupnr, userEmail, recIndex);
		setTimeout(() => {
			navigate(`/receipt/delete/${role}/${groupnr}`);
		}, 500);
	};

	if (userReceipts === undefined) {
		return <div>loading...</div>;
	} else if (userReceipts.length === 0) {
		return (
			<div className="text-justify">
				<h3>you dont have any receipts</h3>
				<h5>add receipt below to share your costs</h5>
			</div>
		);
	} else {
		return (
			<div id="receiptstable" className="d-flex justify-content-center">
				<TableContainer sx={{ maxWidth: 500 }} component={Paper}>
					<Table aria-label="simple table">
						<TableHead align="center">
							<TableRow align="center">
								<TableCell align="center">Note</TableCell>
								<TableCell align="center">Date    </TableCell>
								<TableCell align="center">Amount&nbsp;()</TableCell>
								{!userFinished && <TableCell align="center" />}
							</TableRow>
						</TableHead>
						<TableBody>
							{userReceipts.map((row, index) => (
								<TableRow key={index} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
									<TableCell align="center">{row[0] /*Note*/}</TableCell>
									<TableCell align="center">{row[1] /*Date*/}</TableCell>
									<TableCell align="center">{row[2] /*Amount*/}</TableCell>
									{!userFinished && (
										<TableCell
											align="center"
											sx={{ cursor: 'pointer' }}
											onClick={() => {
												deleteReceipt(index);
											}}
										>
											{<ClearOutlinedIcon />}
										</TableCell>
									)}
								</TableRow>
							))}
						</TableBody>
					</Table>
				</TableContainer>
			</div>
		);
	}
};

export default UserReceiptsTable;
