import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';

function DeleteReceiptPort() {
	const { groupnr, role } = useParams('');
	const navigate = useNavigate();

	React.useEffect(() => {
		setTimeout(() => {
			navigate(`/group/view/${role}/${groupnr}`);
		}, 500);
	}, []);

	return (
		<React.Fragment>
			<h1>deleting receipt...</h1>
		</React.Fragment>
	);
}

export default DeleteReceiptPort;
