import React, { useState } from 'react';
import { APIContext } from '../../backend/APIContext';
import { useNavigate } from 'react-router-dom';
//MUI
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { DialogContentText } from '@mui/material';

function MemberFinish({ groupID, memberID, role, memberReceipts }) {
	const [ open, setOpen ] = useState(false);
	const { memberFinishToggle, receiptFinishToggle } = React.useContext(APIContext);
	const navigate = useNavigate();

	const handleClickOpen = () => {
		setOpen(true);
	};

	const handleClose = () => {
		setOpen(false);
	};

	const finishMember = () => {
		memberFinishToggle(groupID, memberID, 'true');
		receiptFinishToggle(groupID, memberID, memberReceipts, 'finish');
		setTimeout(() => {
			navigate(`/user/finish/${role}/${groupID}`);
		}, 250);
	};

	return (
		<div>
			<Button variant="outlined" onClick={handleClickOpen}>
				I'm All Set!
			</Button>
			<Dialog open={open} onClose={handleClose}>
				<DialogTitle>finish process</DialogTitle>
				<DialogContent>
					<DialogContentText id="alert-dialog-description">
						Are you sure you added all your receipts?
						<br />
						Once confirmed, you cant add receipts anymore
					</DialogContentText>
				</DialogContent>
				<DialogActions>
					<Button onClick={finishMember} autoFocus>
						confirm
					</Button>
				</DialogActions>
			</Dialog>
		</div>
	);
}

export default MemberFinish;
